<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">
      <router-link class='el-button--text' to="/rotation/list" replace>轮播图管理</router-link>
      / {{pageTitle}}
    </el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" label-width="100px" label-suffix=":" label-position="left">
        <el-form-item label="轮播图片" required>
          <div v-if="form.image && form.image.id" class="avatar">
            <img :src="'https://szebra.cn' + form.image.url" />
            <i class="el-icon-circle-close" @click="removeAvatarImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="https://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="资讯类别" required>
          <el-radio-group v-model="form.link_type">
            <el-radio :label="'none'">不链接</el-radio>
            <el-radio :label="'article'">链接公众号文章</el-radio>
            <el-radio :label="'page'">自定义页面</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.link_type == 'article'" label="文章地址" required>
          <el-input v-model="form.articleLink" placeholder="请输入公众号文章地址"></el-input>
        </el-form-item>
        <el-form-item v-if="form.link_type == 'page'" label="链接页面" required>
          <el-select v-model="form.pageLink" filterable placeholder="请选择链接页面">
            <el-option v-for="item in pages" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="onSubmit" style="">{{ submitBtnTitle }}</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  components: {
  },

  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      pageTitle: '', // 页面标题
      submitBtnTitle: '',
      pages: [],
      form: {
        id: '',
        image: {
        },
        link_type: 'none',
        link: '',
        articleLink: '',
        pageLink: ''
      }
    }
  },
  methods: {
    removeAvatarImageUrl () {
      this.form.image = {}
    },
    handleAvatarSuccess(data) {
      this.form.image.id = data.result_rows.id
      this.form.image.url = data.result_rows.url
      console.log(data.result_rows.url, this.form.image.id, this.form.image.url)
    },
    beforeAvatarUpload(file) {
      const isImg = file.type.startsWith('image/');
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isImg) {
        this.$message.error('只允许上传图片');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isImg && isLt2M;
    },
    initForm (data) {
      Object.assign(this.form, data)
      this.form[`${data.link_type}Link`] = data.link
    },
    formatSubmitValue () {
      if (!this.form.image.id) {
        this.$message.error('图片不能为空')
        return
      }
      switch (this.form.link_type) {
        case 'none':
          this.form.link = ''
          break
        case 'page':
          if (!this.form.pageLink) {
            this.$message.error('链接页面不可为空')
            return
          }
          this.form.link = this.form.pageLink
          break
        case 'article':
          if (!this.form.articleLink) {
            this.$message.error('文章地址不可为空')
            return
          }
          this.form.link = this.form.articleLink
      }
      const ret = { id: this.form.id, slider: Object.assign({}, this.form) }
      ret.slider.image_attributes = { id: this.form.image.id }
      ret.slider.image = ret.slider.pageLink = ret.slider.articleLink = undefined
      return ret
    },
    // 提交表单
    onSubmit () {
      const data = this.formatSubmitValue()
      console.log(JSON.stringify(data))
      if (!data) return
      this.$http[this.submitFuncName](data).then(res => {
        if (res.data.retcode == 0) {
          this.initForm(res.data.result_rows)
          this.$message.success('保存成功')
          this.$router.push({ path: '/rotation/list'})
        } else {
          this.$message.error('保存失败');
        }
      })
    }
  },

  mounted () {
    switch (this.$router.currentRoute.name) {
      case 'rotation_create':
        this.pageTitle = '添加轮播图'
        this.submitBtnTitle = '提交'
        this.submitFuncName = 'rotationCreate'
        break
      default:
        this.pageTitle = '编辑轮播图'
        this.submitBtnTitle = '保存所有编辑结果'
        this.submitFuncName = 'rotationUpdate'
        this.$http.rotationDetail(this.$router.currentRoute.params)
            .then(res => {
              if (!res) return
              this.initForm(res.data.result_rows)
            })
    }
    // need fix
    // 自定义页面缺少 link
    this.$http.pageList({ per: 100, page: 0 })
      .then(res => {
        if (!res) return
        Object.assign(this.pages, res.data.result_rows.pages)
    })
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    position: relative;
    float: left;
    margin-right: 5px;
  }
  .avatar img {
    margin-top: 6px;
    margin-right: 6px;
    width: 178px;
    height: 178px;
  }
  .avatar i {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.6;
  }
  .avatar i:hover {
    opacity: 1;
    cursor: pointer;
  }
</style>
